import React from "react";
import Image from "next/image";
import style from "./blogPreview.module.css";

export default function BlogPreview() {
  return (
    <div className={style.div}>
      <h3> Blog Name </h3>
      <div>
        <Image
          src="/images/Cherrim.png"
          alt="Cherrim"
          width={500}
          height={500}
        />
        <p>Blog description</p>
        <p>Posted on...</p>
      </div>
    </div>
  );
}
